import React from 'react'
import styled from 'styled-components'
import logo from '../Images/logo.png';
import Home from '../Pages/home'
import Products from '../Pages/products'
import About from '../Pages/about'
import Contact from '../Pages/contact'
import Projects from '../Pages/projects'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { FaFacebookF, FaInstagram } from 'react-icons/fa'
import { GrMail, GrMenu } from 'react-icons/gr'


const NavStyled = styled.div`
  a {
    text-decoration: none;
    color: #0C0B0C;
  }
  .desktop-nav {
    display: none;
  }
  .mobile-nav {
    display: flex;
    align-items: center;
    padding: 10px;
    justify-content: space-between;
  }
  .mobile-nav-items {
    padding: 40px;
    position: absolute;
    background-color: black;
    width: 100%;
    height: 100%;
    left: -0.2px;
    top: 60px;
    display: grid;
    ul {
      display: grid;
      grid-row-gap: 30px;
      grid-template-columns: repeat(1, 1fr);
    }
    .social {
      margin-top: 50px;
    }
  }
  .link {
    color: white;
    font-size: 15px;
    text-decoration: none;
    margin: 10px 0 0 0;
  }
  .social {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    a {
      color: white;
    }
  }
  @media screen and (min-width: 1024px) {
    .desktop-nav {
      padding: 30px 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .mobile-nav {
      display: none;
    }
    .link {
      margin: 0px 10px;
      color: #0C0B0C;
      display: inline-block;
    }
    .social {
      a {
        color: #0C0B0C;
      }
  }
  }
`
function Nav() {
  function menuAction() {
    const nav = document.getElementById('nav-mobile')
    if (nav.style.display === 'none') {
      nav.style.display = 'block'
    } else {
      nav.style.display = 'none'
    }
  }

  function closeMenu() {
    const nav = document.getElementById('nav-mobile')
    nav.style.display = 'none'
    
  }

  const navItems = [
    {
      name: 'Home',
      route: `${process.env.PUBLIC_URL}/`,
      component: <Home />
    },
    {
      name: 'Productos',
      route: `${process.env.PUBLIC_URL}/productos`,
      component: <Products />
    },
    {
      name: '¿Quiénes Somos?',
      route: `${process.env.PUBLIC_URL}/about`,
      component: <About />
    },
    {
      name: 'Contacto',
      route: `${process.env.PUBLIC_URL}/contacto`,
      component: <Contact />
    },
    {
      name: 'Proyectos',
      route: `${process.env.PUBLIC_URL}/proyectos`,
      component: <Projects />
    }
  ]
  
  return (
    <NavStyled>
      <Router>
        <nav className="desktop-nav">
          <a href={`${process.env.PUBLIC_URL}/`}>
            <img src={logo} alt="logo" width="94px" height="29px"/>
          </a>
          <ul>
            {
              navItems.map((item) => {
                return (
                  <Link className="link" to={item.route} key={item.name}>{item.name}</Link>
                )
              })
            }
          </ul>
          <div className="social">
            <a href="https://instagram.com/akuarium_mendoza?utm_medium=copy_link">
              <FaFacebookF size="18px" style={{cursor:'pointer'}}/>
            </a>
            <a href="https://instagram.com/akuarium_mendoza?utm_medium=copy_link">
              <FaInstagram size="18px" style={{cursor:'pointer'}}/>
            </a>
            <a href="mailto:akuariummza@hotmail.com">
              <GrMail size="18px" style={{cursor:'pointer'}}/>
            </a>
          </div>
        </nav>
        
        <nav className="mobile-nav">
          <a href={`${process.env.PUBLIC_URL}/`}>
            <img src={logo} alt="logo" width="94px" height="29px"/>
          </a>
          <GrMenu size="30px" onClick={menuAction}/>
          <div className="mobile-nav-items" style={{display:"none"}} id="nav-mobile">
            <ul>
              {
                navItems.map((item) => {
                  return (
                    <Link onClick={closeMenu} className="link" to={item.route} key={item.name}>{item.name}</Link>
                  )
                })
              }
            </ul>
            <div className="social">
              <a href="https://instagram.com/akuarium_mendoza?utm_medium=copy_link">
                <FaFacebookF size="18px" style={{cursor:'pointer'}}/>
              </a>
              <a href="https://instagram.com/akuarium_mendoza?utm_medium=copy_link">
                <FaInstagram size="18px" style={{cursor:'pointer'}}/>
              </a>
              <a href="mailto:akuariummza@hotmail.com">
                <GrMail size="18px" style={{cursor:'pointer'}}/>
              </a>
            </div>
          </div>
        </nav>


        <Switch>
          {
            navItems.map((item) => {
              return(
                <Route exact path={item.route} key={item.name}>
                  {item.component}
                </Route>
              )
            })
          }
        </Switch>
      </Router>
    </NavStyled>
  )
}

export default Nav
