import React from 'react'
import styled from 'styled-components'
import Img1 from '../Images/peceras/pecera-1.jpg'
import Img2 from '../Images/peceras/pecera-2.jpg'
import Img3 from '../Images/peceras/pecera-3.jpg'
import Img4 from '../Images/peceras/pecera-4.jpg'
import Img5 from '../Images/peceras/pecera-5.jpg'

const ProjectsStyled = styled.div`
  margin-top: 80px;
  text-align: center;
  h1 {
    font-size: 40px;
    span {
      color: #0cacbc;
    }
  }
  .gallery {
    padding: 20px;
    display: grid;
  }
  img {
    width: 100%;
  }
  @media screen and (min-width: 1024px) {
    .gallery {
      padding: 100px;
      grid-template-columns: repeat(3, 1fr);
    }
    .gallery-grid {
    }
  }
`

function Projects() {
  const fishBowls = [
    {
      img: Img2,
      alt: "pecera2"
    },
    {
      img: Img3,
      alt: "pecera3"
    },
    {
      img: Img4,
      alt: "pecera4"
    },
    {
      img: Img5,
      alt: "pecera5"
    },
    {
      img: Img1,
      alt: "pecera1"
    }
  ]
  return (
    <ProjectsStyled>
      <h1>Proyectos <span>Realizados</span></h1>
      <div className="gallery">
        {
          fishBowls.map(item => {
            return (
              <div key={item.alt} className="gallery-grid">
                <img src={item.img} alt={item.alt}/>
              </div>
            )
          })
        }
      </div>
    </ProjectsStyled>
  )
}

export default Projects
