import React from 'react'
import styled from 'styled-components'
import { useForm, ValidationError } from '@formspree/react';
import { FaPhoneAlt, FaWhatsapp } from 'react-icons/fa'
import { GrMail } from 'react-icons/gr'
import { SiGooglemaps } from 'react-icons/si'

const ContactFormStyled = styled.div`
  padding: 10px;
  margin-top: 50px;
  form {
    padding: 30px;
    display: grid;
    row-gap: 20px;
    font-weight: 500;
    input, textarea{
      outline: none;
      border: solid black 1px;
      border-radius: 5px;
      margin-top: 10px;
    }
    input {
      padding: 5px;
    }
  }
  .sumbit-btn {
    width: 130px;
    outline: none;
    font-weight: 700;
    border: none;
    color: white;
    padding: 10px 40px;
    background-color: #0cacbc;
    margin-top: 10px;
  }
  .form-child {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .social-contact-child {
    display: flex;
    align-items: center;
    margin: 20px 0;
    p {
      margin-left: 10px;
    }
  }
  @media screen and (min-width: 1024px) {
    main {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
    .social-contact {
      margin-top: 20px;
      margin-left: 50px;
    }
  }
`

function ContactForm() {

  const [state, handleSubmit] = useForm("xdoylawy");
  if (state.succeeded) {
      return (
        <div style={{textAlign:'center'}}>
          <h2 style={{marginBottom:"20px"}}>
            <span style={{color:"#0cacbc"}}>Gracias por contactarnos</span>, ya le responderemos
          </h2>
          <a href={`${process.env.PUBLIC_URL}/`}
            style={{border:"2px solid", padding:"10px"}}
            >
            Volver al inicio
          </a> 
        </div>
      );
  }

  return (
    <ContactFormStyled>
      <h1 style={{fontSize:"40px", textAlign:"center"}}>
        <span style={{color:"#0cacbc"}}>Contacte</span> con nosotros
      </h1>
      <main>
        <form onSubmit={handleSubmit}>
          <div className="form-child">
            <label htmlFor="email">Email</label>
            <input id="email" type="email" name="email"/>
            <ValidationError 
              prefix="Email" 
              field="email"
              errors={state.errors}
            />
          </div>

          <div className="form-child">
            <label htmlFor="name">Nombre</label>
            <input id="name" type="text" name="name"/>
            <ValidationError 
              prefix="Name" 
              field="text"
              errors={state.errors}
            />
          </div>

          <div className="form-child">
            <label htmlFor="texr">Deje su consulta</label>
            <textarea id="message" name="message" rows="10"/>
              <ValidationError 
              prefix="Message" 
              field="message"
              errors={state.errors}
            />
          </div>

          <button type="submit" disabled={state.submitting} className="sumbit-btn">
            Enviar
          </button>
        </form>
        
        <section className="social-contact">
          <div className="social-contact-child">
            <FaPhoneAlt size="25px"/>
            <p>+54 4203397</p>
          </div>
          <div className="social-contact-child">
            <FaWhatsapp size="25px"/>
            <p>261 6137418</p>
          </div>
          <div className="social-contact-child">
            <GrMail size="25px"/>
            <p>akuariummza@hotmail.com</p>
          </div>
          <div className="social-contact-child">
            <SiGooglemaps size="25px"/>
            <p>Necochea 565 Ciudad - Mendoza Argentina</p>
          </div>
        </section>
      </main>
    </ContactFormStyled>
  )
}

export default ContactForm
