import React from 'react'
import styled from 'styled-components'

const ProductsStyled = styled.div`
  text-align: center;
  margin: 100px 0;
  h1 {
    color: #0cacbc;
    font-size: 55px;
  }
  p {
    margin: 20px 0;
  }
  @media screen and (min-width: 1024px) {
    h1 {
      font-size: 90px;
    }
  }
`

function Products() {
  return (
    <ProductsStyled>
      <h1>Productos</h1>
      <p>
        Alimentos - Suplementos - Medicación - Equipos - Accesorios - Iluminación
      </p>
    </ProductsStyled>
  )
}

export default Products
