import React from 'react'
import styled from 'styled-components'
import Img from '../Images/about-img.jpg'

const AboutStyled = styled.div`
  margin: 80px 0 0 0;
  text-align: center;
  h1 {
    font-size: 55px;
    color: #0cacbc;
  }
  img {
    width: 100%;
    margin: 50px 0 0 0;
  }
  .about-div {
    margin-top: 60px;
    padding: 50px 10px;
    background-color: #0C0B0C;
    color: white;
    p {
      margin: 30px 0;
      line-height: 30px;
      color: #C1BABB;
    }
  }
  @media screen and (min-width: 1024px) {
    section {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      margin: 50px 0 0 0;
    }
    .about-div {
      margin-top: 0;
      p {
        padding: 80px 10px;
      }
      h2 {
        font-size: 40px;
      }
    }
    img {
      margin: 0;
      height: 100%;
    }
  }
`

function About() {
  return (
    <AboutStyled>
      <h1>Akuarium</h1>
      <section>
        <div>
          <img src={Img} alt="nosotros"/>
        </div>
        <div className="about-div">
          <h2>¿Quiénes somos?</h2>
          <p>
            Akuarium es una empresa que nace en el año 2009 
            materializando 15 años de experiencia en el rubro 
            del acuarismo. Nuestro local cuenta con dos pisos, un 
            sector de exposición de trabajos terminados, y la más 
            amplia variedad de peces nacionales e importados, como 
            asi tambien la mayor variedad de marcas en alimentos y 
            accesorios para los más exigentes acuaristas de Argentina.
            También contamos con un taller acondicionado para el 
            trabajo con vidrio, madera y metal donde se realiza el 
            armado de acuarios, filtros y muebles para acuarios 
            familiares, empresas, consultorios, estudios, hoteles 
            y restaurantes.
          </p>
        </div>
      </section>
    </AboutStyled>
  )
}

export default About
